import { IoMdCloseCircle } from "react-icons/io";
import { FaCheckCircle } from "react-icons/fa";

const AddAccount = ({ setAddAccount }: { setAddAccount: any }) => {
  return (
    <section className="addAccount">
      <div className="add-header">
        <p>Agregar cuenta</p>
        <IoMdCloseCircle
          onClick={() =>
            setAddAccount((account: boolean) => (account ? false : true))
          }
        />
      </div>
      <div className="add-amount">
        <label htmlFor="amount">saldo actual</label>
        <div>
          <p>{"MXN"}</p>
          <input id="amount" type="number" placeholder="00,000.00" />
        </div>
      </div>
      <div className="add-name">
        <input type="text" placeholder="Nombre de la cuenta" />
      </div>
      <div className="add-row">
        <label htmlFor="bank">Institución bancaria:</label>
        <select name="bank" id="bank">
          <option value="">Banamex</option>
        </select>
      </div>
      <div className="add-row">
        <label htmlFor="type">Tipo de cuenta:</label>
        <select name="type" id="type">
          <option value="1">Cuenta corriente</option>
        </select>
      </div>
      <div className="add-footer">
        <FaCheckCircle />
      </div>
    </section>
  );
};

export default AddAccount;
